.main {
    width: 120px;
    height: 100px;
    display: flex;
    align-items: center;
    font-family: 'Bebas Neue', cursive;
    font-size: 30px;
    letter-spacing: 10px;
    position: absolute;
    left: 35%;
    bottom: 65px;
    justify-content: flex-start;
    // background-color: pink;
    text-decoration: none;
}

.main:hover {
    cursor: pointer;
    transition: 0.4s;
	opacity: 0.4;
}
