/* 3D Slideshow */
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
.slideshowon {
	width: 100vw;
	height: 100vh;
    display: flex;
    justify-content: center;
	align-items: center;
	box-sizing: border-box;
	position: absolute;
	top: 0;
	left: 0;
	// background-color: gray;
	z-index: 1;
	overflow: hidden;
}

.slideshowoff {
	width: 100vw;
	height: 100vh;
    display: none;
    justify-content: center;
	align-items: center;
	box-sizing: border-box;
	position: absolute;
	top: 0;
	left: 0;
	// background-color: gray;
	z-index: 1;
	overflow: hidden;
}

.entire-content {
	width: 1000px;
	perspective: 1000px;
	position: relative;
  	display: flex;
  	justify-content: center;
  	align-items: center;
  	// background-color: blue;
}

.content-carrousel {
	width: 60%;
	position: absolute;
	float: right;
	animation: rotar 30s infinite linear;
	transform-style: preserve-3d;
	display: flex;
	justify-content: center;
	align-items: center;
}

.content-carrousel1 {
	width: 100%;
	position: absolute;
	float: right;
	animation: rotar1 0.5s 1 ease-in;
	animation-fill-mode: forwards;
	transform-style: preserve-3d;
	display: flex;
	justify-content: center;
	align-items: center;
}

