.shadowBrightness {
	filter: brightness(20%);
	box-shadow: 0px 0px 0px 0px #000; 
}

@keyframes rotar {
	from {
		transform: rotateY(0deg);
	} to {
		transform: rotateY(360deg);
	}
}

@keyframes rotar1 {
	 to {
		transform: rotateY(0);
	}
}

@keyframes rotar2 {
	to {
	   transform: rotateY(-40deg);
   }
}

@keyframes rotar3 {
	to {
	   transform: rotateY(-80deg);
   }
}

@keyframes rotar4 {
	to {
	   transform: rotateY(-120deg);
   }
}

@keyframes rotar5 {
	to {
	   transform: rotateY(-160deg);
   }
}

@keyframes rotar6 {
