.quitbtn {
	width: 100px;
	height: 50px;
	position: absolute;
	bottom: 90px;
	font-size: 25px;
	display: flex;
	justify-content: center;
    align-items: center;
	font-family: 'Bebas Neue', cursive;
}

.quitbtn:hover {
	cursor: pointer;
	transition: 0.4s;
	opacity: 0.3;
}

.inc {
	width: 100px;
	height: 50px;
	position: absolute;
	right: 5%;
	bottom: 50%;
	font-size: 30px;
	display: flex;
	justify-content: center;
  align-items: center;
	font-family: 'Bebas Neue', cursive;
	background-color: transparent;
}

.inc:hover {
	cursor: pointer;
	transition: 0.4s;
	opacity: 0.4;
}

.desc:hover {
	cursor: pointer;
	transition: 0.4s;
	opacity: 0.4;
}

.desc{
	width: 100px;
	height: 50px;
	position: absolute;
	left: 5%;
	bottom: 50%;
	font-size: 30px;
	display: flex;
	justify-content: center;
  align-items: center;
	font-family: 'Bebas Neue', cursive;
	background-color: transparent;
}

.detail {
	width: 100px;
	height: 50px;
	position: absolute;
	left: 50%;
	bottom: 90px;
	font-size: 25px;
	transform: translateX(-50%);
	display: flex;
	justify-content: center;
  align-items: center;
	font-family: 'Bebas Neue', cursive;
	color: inherit;
}

.detail:hover {
	cursor: pointer;
	transition: 0.4s;
	opacity: 0.3;
}



.header_box {
  width: 100%;
  height: 100px;
  position: absolute;
  left: 0;
  top: 0;
  font-family: 'Bebas Neue', cursive;
  // background-color: pink;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 100;
  color: #bbc4b9;
}

.header_title {
  width: 200px;
  height: 50px;
  // background-color: gray;
  display: flex;
  align-items: center;
  font-size: 30px;
  margin-left: 50px;
}

.header_index {
  width: 500px;
  height: 50px;
  // background-color: gray;
  display: flex;
  margin-right: 50px;
}

.header_index .about {
  width: 33.33%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  // background-color: blue;
}

.header_index .work {
  width: 33.33%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  // background-color: blue;
}

.header_index .contact {
  width: 33.33%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 20px;
  text-decoration: none;
  // background-color: blue;
}

.work:hover, .about:hover, .contact:hover {
	cursor: pointer;
	transition: 0.4s;
	opacity: 0.3;
};


.about:visited {
  text-decoration: none;
  color: inherit;
}

.work:visited {
  text-decoration: none;
  color: inherit;
}

.contact:visited {
  text-decoration: none;
  color: inherit;
}

.header_index a:hover {
  cursor: pointer;
}

.footer_box {
    width: 100%;
    height: 50px;
    position: absolute;
    left: 0;
    bottom: 0;
    // background-color: pink;
    font-family: 'Bebas Neue', cursive;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    color: #bbc4b9;
  }
  
.footer_title {
    width: 500px;
    height: 50px;
    // background-color: gray;
    display: flex;
    align-items: center;
    font-size: 20px;
    margin-left: 50px;
    overflow: hidden;
  }
  
.footer_index {
    width: 200px;
    height: 50px;
    // background-color: gray;
    display: flex;
    margin-right: 50px;
  }
.footer_index a {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    // background-color: blue;
    text-decoration: none;
    color: inherit;
  }
  
  a:visited {
    color: inherit;
  }
  
  a:active {
    color: inherit;
  }
  
  .footer_index a:hover {
    cursor: pointer;
		transition: 0.4s;
		opacity: 0.3;
  }


