@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+Display&display=swap');


.contacton {
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  font-family: 'Noto Sans Display', sans-serif;
  color: #bbc4b9;
  // background-color: pink;
}

.contactoff {
  width: 100vw;
  height: 100vh;
  display: none;
  font-family: 'Noto Sans Display', sans-serif;
  // background-color: pink;
}

.contactbox {
  width: 75%;
  height: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  // background-color: blue;
  z-index: 100;
}

.contactbox h1 {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  // margin: 0;
  // padding: 0;
  font-size: 80px;
  letter-spacing: 5px;
  // background-color: red;
}

.info {
  width: 50%;
  height: 60%;
  margin-top: -100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  // background-color: yellow;
}

.info div {
  width: 100%;
  height: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 30px;
}

